<template>
  <div>
    <button class="btn btn-primary" @click="showModal">
      <i class="fe fe-plus"></i>
      Ajouter un espace de dpt
    </button>

    <confirm-modal-with-wait ref="modal"
                             cancel-button="Annuler"
                             confirm-button="Crer l'espace de dpt"
                             title="Crer un nouvel espace de dpt"
                             @confirm="createControl"
    >
      <div>
        <info-bar>
          Chaque espace de dpt n'est visible que par les personnes que vous inviterez.
        </info-bar>

        <form>
          <div class="form-group mb-6">
            <label id="title-label" class="form-label">
              Quel est le nom de la procdure pour laquelle vous ouvrez cet espace de dpt ?
              <span class="form-required">*</span>
            </label>
            <div id="title-help" class="text-muted">
              Exemple : Contrle des comptes et de la gestion de la Fdration Franaise de
              Football. 255 caractres maximum.
            </div>
            <div class="flex-row align-items-center">
              <i class="fa fa-award mr-2 text-muted"></i>
              <input type="text"
                     class="form-control"
                     v-model="title"
                     maxlength="255"
                     required
                     aria-describedby="title-help"
                     aria-labelledby="title-label">
            </div>
          </div>

          <div class="form-group mb-6">
            <label id="organization-label" class="form-label">
              Quel est le nom de lorganisme qui va dposer les rponses ?
              <span class="form-required">*</span>
            </label>
            <div id="organization-help" class="text-muted">
              Exemple : Ministre des Sports. 255 caractres maximum.
            </div>
            <div class="flex-row align-items-center">
              <i class="fa fa-building mr-2 text-muted"></i>
              <input type="text"
                     class="form-control"
                     v-model="organization"
                     maxlength="255"
                     required
                     aria-describedby="organization-help"
                     aria-labelledby="organization-label">
            </div>
          </div>

          <div class="form-group mb-6">
            <label id="reference-code-label" class="form-label">
              Indiquez un nom abrg pour cet espace de dpt :
              <span class="form-required">*</span>
            </label>
            <div id="reference-code-help" class="text-muted">
              Ce nom sera celui du dossier contenant les pices dposes. Il apparatra dans votre
              explorateur Windows. Nous conseillons un nom court (max 25 caractres) et signifiant,
              pour que vous retrouviez facilement le dossier. Exemple : FFF_MinSports
            </div>
            <div class="input-group">
            <span class="input-group-prepend" id="basic-addon3">
              <span class="input-group-text">{{ reference_code_prefix }}</span>
            </span>
              <input type="text" class="form-control" v-model="reference_code_suffix" required
                     pattern="^[\.\s\w----]+$"
                     maxlength="25"
                     title="Ce champ ne doit pas contenir de caractres spciaux
                         ( ! , @ # $ / \ ' &quot; + etc)"
                     aria-describedby="reference-code-help"
                     aria-labelledby="reference-code-label">
            </div>
            <span class="text-danger" v-if="reference_code_suffix.length > 24">
              Ce champ ne peut contenir plus de 25 caractres.
            </span>
          </div>

        </form>
      </div>
    </confirm-modal-with-wait>
  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'

import backendUrls from '../utils/backend'
import ConfirmModalWithWait from '../utils/ConfirmModalWithWait'
import InfoBar from '../utils/InfoBar'

axios.defaults.xsrfCookieName = 'csrftoken'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'

export default Vue.extend({
  data: function() {
    return {
      title: '',
      organization: '',
      reference_code_suffix: '',
      year: new Date().getFullYear(),
    }
  },
  computed: {
    reference_code_prefix: function () {
      return this.year + '_'
    },
  },
  components: {
    ConfirmModalWithWait,
    InfoBar,
  },
  methods: {
    showModal() {
      $(this.$refs.modal.$el).modal('show')
    },
    createControl: function(processingDoneCallback) {
      const payload = {
        title: this.title,
        depositing_organization: this.organization,
        reference_code: this.reference_code_prefix + this.reference_code_suffix,
      }
      axios.post(backendUrls.control(), payload)
        .then(response => {
          console.debug(response)
          processingDoneCallback(null, response)
          window.location.href = backendUrls.home()
        })
        .catch((error) => {
          console.error('Error creating control', error)
          const errorMessage = this.makeErrorMessage(error)
          processingDoneCallback(errorMessage)
        })
    },
    makeErrorMessage: function (error) {
      if (error.response && error.response.data && error.response.data.reference_code) {
        const requestedCode = JSON.parse(error.response.config.data).reference_code
        if (error.response.data.reference_code[0] === 'UNIQUE') {
          return 'Le nom abrg "' + requestedCode +
                '" existe dj pour un autre espace. Veuillez en choisir un autre.'
        }
        if (error.response.data.reference_code[0] === 'INVALID') {
          return 'Le nom abrg "' + requestedCode +
                 '" ne doit pas contenir de caractres spciaux (! , @ # $ / \\ " \' + etc).' +
                 ' Veuillez en choisir un autre.'
        }
      }

      if (error.message && error.message === 'Network Error') {
        return "L'espace de dpt n'a pas pu tre cr. Erreur : problme de rseau"
      }

      if (error.message) {
        return "L'espace de dpt n'a pas pu tre cr. Erreur : " + error.message
      }

      return "L'espace de dpt n'a pas pu tre cr."
    },
  },
})

</script>
